.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    background: black;
    height: 209px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  
    @include media-query(medium) {
      justify-content: end;
    }
  
    &__text {
      color: $white;
      font-weight: 500;
      font-size: 12px;
      @include media-query(small) {
        font-size: 16px;
      }
      @include media-query(medium) {
        font-size: 20px;
        margin-top: 40px;
        margin-bottom: 30px;
      }
      @include media-query(large) {
        font-size: 24px;
      }
    }
}